import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

/**
 * Note: 
 *
 * hidden: true                     //  true  401login/edit/1
 * alwaysShow: true                 //  children 1--
 *                                  // --
 *                                  //  children 
 *                                  //  alwaysShow: true
 * redirect: noRedirect             //  noRedirect 
 * name:'router-name'               // <keep-alive>
 * query: '{"id": 1, "name": "ry"}' // 
 * roles: ['admin', 'common']       // 
 * permissions: ['a:a:a', 'b:b:b']  // 
 * meta : {
    noCache: true                   // true <keep-alive> ( false)
    title: 'title'                  // 
    icon: 'svg-name'                // src/assets/icons/svg
    breadcrumb: false               // falsebreadcrumb
    activeMenu: '/system/user'      // 
  }
 */

// 
export const constantRoutes = [
  {
    path: "/test",
    component: () => import("@/views/test")
  },
  {
    path: "/",
    component: () => import("@/views/gen/index.vue")
  },
  {
    path: "/gen-edit/index/:tableId",
    component: () => import("@/views/gen/editTable.vue")
  },
  {
    path: "/build",
    component: () => import("@/views/build/index.vue")
  },
  {
    path: "/text",
    component: () => import("@/views/text/index.vue")
  }
];

// 
let routerPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(err => err);
};

export default new Router({
  mode: "hash", // url#
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
});



